import { useState, useEffect } from 'react';
import { storage, storageRef, fireStoreRef, timeStamp } from '../firebase/firebase';


const addStorage = (file) => {
    const [progress, setProgress] = useState(0);
    const [error, setError] = useState(null);
    const [url, setUrl] = useState(null);

    const root = storageRef.child('images/');
    const storageReference =  storageRef.child('images/'+file.name);

    
    let checkDuplicates = [];
    
    useEffect(() => {

        // create array of file names in Firebase Storage.
        root.listAll().then((result)=> {
            result.items.forEach((item) => {
                checkDuplicates.push(item.name)
            })
        }).then(()=>{
            // upload file to the Storage
            storageReference.put(file).on('state_changed', (snap) => {
                // Check if Firebase storage has duplicated file name.
                if (!checkDuplicates.includes(file.name)) {
                let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
                setProgress(percentage);
                }
            }, (err) => {
                setError(err)
            }, async () => {

                // when upload is done.
                const url = await storageReference.getDownloadURL();

                // Check if Firebase Cloud has duplicated file name.
                if (!checkDuplicates.includes(file.name)) {
                    fireStoreRef.add({ url, createdAt: timeStamp(), name: file.name, type: file.type })
                    .then(()=>{
                        setUrl(url);
                    })
                } else {
                    alert('File name already exists. Please change the file name.');
                    setError('err');
                }
                // the above 'url' does not override hook 'url' because different scope.

            })
        })
    }, [file]);

    return { progress, error, url }

}


export default addStorage;