import React, { useEffect } from 'react';
import addStorage from '../../hooks/addStorage';

const ProgressBar = ({ file, setFile }) => {

    const { url, progress } = addStorage(file);

    // need to reset file to remove ProgressBar when url is there (it means uploaded)  
    useEffect(()=>{

        if(url) {
            setFile(null);
        }

    }, [url])

    return (
        <div className="progress-bar" style={{width: progress + '%'}}>
            Progress
        </div>
    )
}

export default ProgressBar;