import React, { useState, useEffect } from 'react';
import {fireStoreRef, storage } from '../../firebase/firebase';

const AdminCard = ({ tractChecked, setTrackChecked, url, name, id, createdAt, index }) => {
    
    const [file, setFile] = useState();
    // track checkedbox with array
    // const handleBoxChecked = (e) => {
    //     if (e.target.checked){
    //         setDeleteImg([...deleteImg, e.target.value])
    //     } else if (!e.target.checked) {
    //         setDeleteImg([...deleteImg].filter((item) => {return item !== e.target.value}))
    //         console.log('unchecked')
    //     }
    // }

    // track checkedBox with object
    const handleBoxChecked = (e) => {
        const name = e.target.value;

        if (e.target.checked) {
            setTrackChecked({
                ...tractChecked,
               [name] : true
            })
        } else if (!e.target.checked) {
            setTrackChecked({
                ...tractChecked,
               [name] : false
            })
        }
    }
    const handleIndividualDelete = (e) => {
        e.stopPropagation();
        let id = e.target.id;

        // delte image from firebase Cloud and Store
        fireStoreRef.doc(id).delete();
        storage.ref().child('images/'+ e.target.value).delete();
    }

    

    return (
        <div className="admin-card" >
            <label className="check-box">
                {/* Checkbox was giving changing an uncontrolled input error due to undefined checked value */}
                <input type="checkbox" value={name} checked={tractChecked[name] ? tractChecked[name] : false} onChange={handleBoxChecked} />
            </label>
            <p>{index}</p>
            <img src={url} alt='product image' className="admin-product-image" />
            <p>Product: {name}</p>
            <button onClick={handleIndividualDelete} id={id} value={name} >Delete</button>
        </div>
    )
}

export default AdminCard;