import React, { useState } from 'react';
import ProgressBar from './ProgressBar';

const UploadForm = () => {
    
    const [file, setFile] = useState(null);
    const [error, setError] = useState(null);


    const allowedType = ['image/png','image/jpg', 'image/jpeg', 'image/svg']

    const wait = () => {
            return new Promise((resolve) => {
                setTimeout(()=>{
                    resolve()
                }, 100)
            })
        }


    // save multipe files -> (Error) Can't perform a React state update on an unmounted component 
    const FileChangeHandler = async (e) => {
        // let selected = e.target.files[0]
        let selectedObject = e.target.files

        for (let selected in selectedObject) {
            
            if (allowedType.includes(selectedObject[selected].type)){
                setFile(selectedObject[selected]);
                setError(null);
            } else {
                setFile(null);
                setError('Please select png,jpg, jpeg or svg');
            }
            await wait();
        }
    }

    // const delayLog = async (selectedObject) => {
    //     for (let selected in selectedObject) {
    //         if (selected && allowedType.includes(selectedObject[selected].type)){
    //             setFile(selectedObject[selected]);
    //             setError(null);
    //             await wait();
    //         } else {
    //             setFile(null);
    //             setError('Please select png,jpg, jpeg or svg');
    //         }
        
    //     }
    // }

    // const FileChangeHandler = async (e) => {
    //     // let selected = e.target.files[0]
    //     let selectedObject = e.target.files;
    //     const promises = []

    //     // Promise.all requires an array
    //     for (let selcted in selectedObject) {
    //         promises.push(delayLog(selectedObject))
    //     }
        
    //     // make async parallel
    //     await Promise.all(promises)
    // }

    return (
        <form >
            <input type="file" onChange={FileChangeHandler} multiple />
            <div className="output">
                {error && <div className="fileError">{ error }</div>}
                {file && <div className="fileSelected">{ file.name }</div>}
                {file && <ProgressBar file={file} setFile={setFile} />}
            </div>

        </form>
    )
}

export default UploadForm;