import React, { useState, useEffect } from 'react';
import useFireStore from '../../hooks/useFireStore';
import { fireStoreRef, storage } from '../../firebase/firebase';
import AdminCard from './AdminCard';

const AdminWorkGrid = () => {

    const { docs } = useFireStore('images');
    const [tractChecked, setTrackChecked] = useState({});

    let trackCheckedObj = {}

    // create [name]: [checked] object to track what is checked
    useEffect(() => {
        docs && docs.map((doc)=>{
            return trackCheckedObj[doc.name] = false
        })
        setTrackChecked(trackCheckedObj)
    }, [docs])

    // check all checkbox and change state to all true
    const handleSelectAll = () => {
        trackCheckedObj = {...tractChecked}
        for (const item in trackCheckedObj){
            trackCheckedObj[item] = true
        }
        setTrackChecked(trackCheckedObj)
    }

    const handleSelectNone = () => {
        trackCheckedObj = {...tractChecked}
        for (const item in trackCheckedObj){
            trackCheckedObj[item] = false
        }
        setTrackChecked(trackCheckedObj)
    }

    const handleMultipleDelete = () => {
        if (tractChecked) {
            for (const imageName in tractChecked) {
                if (tractChecked[imageName]){
                    const id = docs.find(doc => doc.name === imageName)

                    fireStoreRef.doc(id.id).delete()
                    storage.ref().child('images/'+ imageName).delete();
                }
            }
        }
    }

    return (
        <div id="overview-page">
            <div className="checkbox-control-container">
                <button onClick={handleSelectAll}>Select All</button>
                <button onClick={handleSelectNone}>Select None</button>
                <button onClick={handleMultipleDelete}>Delete</button>
            </div>
            <div className="admin-cards-container">
                {docs && docs.map((doc, index) => {
                    return <AdminCard key={index}
                    tractChecked={tractChecked}
                    setTrackChecked={setTrackChecked}
                    index={index} {...doc} />
                })
                }
            </div>
        </div>
    )
}

export default AdminWorkGrid;